
/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: var(--clr-grey-10);
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
/*  global classes */

/* section */
.section {
  width: 100vw;
  margin: 0 auto;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section {
    width: 100vw;
  }
}

/*
=============== 
Reviews
===============
*/
main {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  place-items: center;
  width: 100vw;
  position: relative;
}
.title {
  text-align: center;
  margin-bottom: 4rem;
}
.underline {
  height: 0.25rem;
  width: 5rem;
  background: var(--clr-primary-5);
  margin-left: auto;
  margin-right: auto;
}
.container {
  position: absolute;
  top: 10vh;
  width: 100vw;
}
.data-container{
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 98%;
  overflow: hidden;
  overflow: auto;  
  table{
    min-width: 900px;
    border-collapse: collapse;
    thead{
      tr{
        th{
          border: none;
          margin: 0;
          border-bottom: 2px solid  var(--clr-primary-5);
          padding: 12px 20px;
        }
      }
      
    }
    tbody{
      tr{
        td{
          padding: 12px 20px;
          text-align: center;
          
        }
      }
    }
  }

}
.data {
  display: flex;
  align-items: center;
  width: fit-content;
  gap: 2rem;
  background: var(--clr-white);
  padding: 2rem 4rem;
  border-radius: var(--radius);
  box-shadow: var(--light-shadow);
  transition: var(--transition);
  text-align: center;
}
.data:hover {
  box-shadow: var(--dark-shadow);
}
.img-container {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 0;
  background: var(--clr-primary-5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.person-img {
  width: 100%;
  display: block;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
}
.quote-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 2.5rem;
  height: 2.5rem;
  display: grid;
  place-items: center;
  border-radius: 50%;
  transform: translateY(25%);
  background: var(--clr-primary-5);
  color: var(--clr-white);
}
.img-container::before {
  content: '';
  width: 100%;
  height: 100%;
  
  position: absolute;
  top: -0.25rem;
  right: -0.5rem;
  border-radius: 50%;
}
.author {
  margin-bottom: 0.25rem;
}



form input{
  border: none;
  outline: none;
  border-bottom: 1px solid var(--clr-primary-5);
  background: var(--clr-primary-10);
  width: 90px;
  
}
.go-btn{
  border-color: transparent;
  background: var(--clr-primary-10);
  color: var(--clr-primary-5);
  cursor: pointer;
}
.urdu{
  font-family: jameel noori nastaleeq,JM nastaleeq;
  direction: rtl;
}
p.urdu{
  text-align: justify;
}
.container{
  position: relative;
  width: 100vw !important; 
  
}
.img-container h3{
  color: var(--clr-white);
  font-size: 40px;
}
.img-container h1{
  color: var(--clr-white);
}

.arabic{
  font-family: norehuda,NRhuda;
}
.plus-icon{
  font-size: 50px;
  cursor: pointer;
  color: var(--clr-primary-5);
}
.content-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.add-button-div{
  background-color: var(--clr-primary-5);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 16px 32px;
  border-radius: 0.25rem;
  box-shadow: var(--light-shadow);
  cursor: pointer;
}
.bottom-side{
  position: fixed;
  z-index: +1;
  bottom: 1.5rem;
  right: 1.5rem;
}
.add-button-div h4{
  margin: 0;
  padding: 0;
  transition: var(--transition);

}
.add-button-div:hover{
  box-shadow: var(--dark-shadow);
}
.black-wall{
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: +2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup-content-container{
  background-color: #fff;
  border-radius: var(--radius);
  box-shadow: var(--light-shadow);
  max-width: var(--fixed-width);
  height: 80vh;
  padding: 3rem;
  p{
    margin: 0;
    font-weight: 700;
  }
  .radio-container{
    display: block;
    margin-top: 1.5rem;
    margin-bottom: 2rem;
    p{
      margin-bottom: 1rem;
      font-size: 18px;
    }
    input{
      cursor: pointer;
    }
    label{
      margin-right: 1rem;
      margin-left: 0.35rem;
      cursor: pointer;
    }

    
  }
}
.popup-content-container h3{
  text-align: center;
}
.popup-content-container form{
  margin-top: 4rem;
}
.popup-content-container form input,.form-description-field{
  outline: none;
  width: 100%;
  font-size: 18px;
  padding: 16px 32px;
  border: 1px solid var(--clr-primary-5);
  margin-bottom: 2rem;
}
.form-description-field{
  height: 150px;
  resize: none;
  margin-bottom: 0;
}
.popup-content-container form .radio-input{
  margin: 0;
  width: initial;
  margin-right: 0.25rem;

}
@media screen and (max-width: 950px){
  .black-wall{
    background-color: #fff;
    height: 150%;
    overflow: hidden;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
@media screen and (max-width: 992px){
  .data-container{
    display: block;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
  }
}